import React, {useEffect} from 'react';
import styled from 'styled-components';
import languages from '../translate/languages';
import {Translate} from 'react-i18nify';
import ReactAudioPlayer from 'react-audio-player';
import Sub from "../subtitle/sub";
import {secondToTime, uuidv4, uploadFile} from '../utils';
import md5 from "md5";


const Tool = styled.div`
    flex: 1;
    width: 100%;
    display: flex;
    flex-direction: column;
    font-size: 13px;
    background-color: rgb(35 40 64);
    margin-bottom: 20px;
    overscroll-behavior: contain;
    -ms-scroll-chaining: contain;
    overflow: auto;

    .item {
        display: flex;
        align-items: center;
        padding: 5px 10px;
        border-bottom: 1px solid rgba(0, 0, 0, 0.4);

        .title {
            margin-right: 15px;
        }

        .value {
            display: flex;
            align-items: center;

            select {
                outline: none;
                margin-right: 15px;
            }

            button {
                height: 25px;
                border: none;
                padding: 0 10px;
                margin-right: 15px;
                outline: none;
                cursor: pointer;
                font-size: 12px;
                color: #fff;
                border-radius: 3px;
                background-color: rgb(26, 83, 109);
                transition: all 0.2s ease;
                &:hover {
                    color: #fff;
                    background-color: #2196f3;
                }
                i {
                    margin-right: 5px;
                }
            }

            input[type='checkbox'] {
                outline: none;
            }

            input[type='range'] {
                height: 3px;
                width: 100px;
                outline: none;
                appearance: none;
                background-color: rgba(255, 255, 255, 0.2);
            }

            .sub-item {
                display: flex;
                margin-right: 15px;

                .sub-title {
                    display: flex;
                    align-items: center;
                    margin-right: 5px;
                    font-size: 12px;
                    color: #888;
                }

                .sub-value {
                    display: flex;
                    align-items: center;
                }
            }
        }
    }
`;


const auditToText = async (blob, updateSubtitles, type) => {

    let fileKey = await uploadFile(blob)
    let secret = "fytubjnf" + "tyguhjn56" + "7uygkjn6" + "5f7ygui";
    let formdata = new FormData();
    let requestId = "web" + uuidv4();
    formdata.append("fileKey", fileKey);
    formdata.append("type", type);
    formdata.append("sign", md5(secret + requestId + fileKey));
    formdata.append("id", requestId);

    let res = fetch("https://kmovie-subtitle-api.corp.kuaishou.com/rest/n/kmovie/audio/recognitionAudioToText", {
        method: 'POST',
        body: formdata,
    }).then(response => {
        return response.json();
    }).then(data => {
        updateSubtitles(data.text.map(item => new Sub(secondToTime(item.startTime), secondToTime(item.endTime), item.text)));
    })
        .catch(error => console.log(error));
}

export default function ({player, storage, language, options, setOption, translateSubtitles, timeOffsetSubtitles, updateSubtitles}) {
    const fontSize = storage.get('fontSize') || 20;
    const bottom = storage.get('bottom') || 40;
    const color = storage.get('color') || {
        r: '255',
        g: '255',
        b: '255',
        a: '1',
    };

    useEffect(() => {
        if (player) {
            const {$subtitle} = player.template;
            $subtitle.style.fontSize = `${fontSize}px`;
            $subtitle.style.bottom = `${bottom}px`;
            $subtitle.style.color = `rgba(${color.r}, ${color.g}, ${color.b}, ${color.a})`;
        }
    }, [bottom, fontSize, player, storage, color]);


    return (
        <Tool>
            <div className="item">
                <div className="title">
                    <Translate value="speech-recognition"/>
                </div>
                <div className="value">
                    <button onClick={() => auditToText(options.audioblob, updateSubtitles, 1)}>
                        <Translate value="music-recognition"/>
                    </button>
                    <button onClick={() => auditToText(options.audioblob, updateSubtitles, 0)}>
                        <Translate value="common-recognition"/>
                    </button>
                    <ReactAudioPlayer style={{height: "20px", width: "220px"}} src={options.audioblob}
                                      controls>
                    </ReactAudioPlayer>
                </div>
            </div>
            <div className="item">
                <div className="title">
                    <Translate value="google-translate"/>
                </div>
                <div className="value">
                    <select style={{color: "#000000"}}
                            value={options.translationLanguage}
                            onChange={(event) => setOption({translationLanguage: event.target.value})}
                    >
                        {(languages[language] || languages.en).map((item) => (
                            <option key={item.key} value={item.key}>
                                {item.name}
                            </option>
                        ))}
                    </select>
                    <button onClick={() => translateSubtitles()}>
                        <Translate value="confirm"/>
                    </button>
                </div>
            </div>
            <div className="item">
                <div className="title">
                    <Translate value="time-offset"/>
                </div>
                <div className="value">
                    <button onClick={() => timeOffsetSubtitles(-0.1)}>-100ms</button>
                    <button onClick={() => timeOffsetSubtitles(0.1)}>+100ms</button>
                    <button onClick={() => timeOffsetSubtitles(-1)}>-1000ms</button>
                    <button onClick={() => timeOffsetSubtitles(1)}>+1000ms</button>
                </div>
            </div>
        </Tool>
    );
}
