import DT from 'duration-time-conversion';
import {toast} from 'react-toastify';

export function isMobile() {
    return /Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(window.navigator.userAgent);
}

export function getExt(url) {
    if (url.includes('?')) {
        return getExt(url.split('?')[0]);
    }

    if (url.includes('#')) {
        return getExt(url.split('#')[0]);
    }

    return url
        .trim()
        .toLowerCase()
        .split('.')
        .pop();
}

export function sleep(ms = 0) {
    return new Promise(resolve => setTimeout(resolve, ms));
}

export function secondToTime(seconds = 0) {
    return DT.d2t(seconds.toFixed(3));
}

export function timeToSecond(time) {
    return DT.t2d(time);
}

export function downloadFile(url, name) {
    const elink = document.createElement('a');
    elink.style.display = 'none';
    elink.href = url;
    elink.download = name;
    document.body.appendChild(elink);
    elink.click();
    document.body.removeChild(elink);
}

export function notify(text = '', type = 'info') {
    // info success warning error default
    return (
        text.trim() &&
        toast[type](text, {
            position: 'top-center',
            autoClose: 2000,
            hideProgressBar: false,
            closeOnClick: true,
            pauseOnHover: true,
            draggable: true,
        })
    );
}

export function getKeyCode(event) {
    const tag = document.activeElement.tagName.toUpperCase();
    const editable = document.activeElement.getAttribute('contenteditable');
    if (tag !== 'INPUT' && tag !== 'TEXTAREA' && editable !== '' && editable !== 'true') {
        return Number(event.keyCode);
    }
}

export function toHex(number) {
    let res = (number).toString(16);
    return res.length===1 ? "0"+res : res;
}

export async function syntheticVideo(options,storage) {
    let videoBlobId=await uploadVideoFile(options.videoUrl);
    let fontColor = storage.get("color");
    let params = {
        "supportTranslate": false,
        "videoWidth": storage.get("metadata").videoWidth,
        "videoHeight": storage.get("metadata").videoHeight,
        "templateId": options.templateId,
        "requestId": uuidv4(),
        "showAss": false,
        "primaryColour": "&H00"+toHex(fontColor.b)+toHex(fontColor.g)+toHex(fontColor.r),
        "fontId": options.fontId,
        "syntheticVideo": true,
        "alignment": 2,
        "marginv": storage.get("bottom"),
        "fontsize": storage.get("fontSize"),
        "blobKey": videoBlobId,
        "lines": storage.get("subtitles").map(item => {
            return {"startTime": timeToSecond(item.start).toFixed(2), "endTime": timeToSecond(item.end).toFixed(2), "words": item.text}
        })
    };
    let rawResponse = await fetch('https://kmovie-subtitle-api.corp.kuaishou.com/rest/n/app/ks/grpc/createAssContent', {
        method: 'POST',
        headers: {
            'Accept': 'application/json',
            'Content-Type': 'application/json'
        },
        body: JSON.stringify(params)
    });
    let content = await rawResponse.json();
    return content.videoUrl;
}

export function uuidv4() {
    return 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, function (c) {
        var r = Math.random() * 16 | 0, v = c == 'x' ? r : (r & 0x3 | 0x8);
        return v.toString(16);
    });
}

export const uploadFile = async (blob) => {
    let formdata = new FormData();
    let blobFile = await fetch(blob).then(r => r.blob());
    blobFile.lastModifiedDate = new Date();
    blobFile.name = new Date().getTime() + ".wav";

    formdata.append('file', blobFile, blobFile.name);
    let res = await fetch("https://api.kuai-ying.com/rest/k/file/blobstore/uploadImage", {
        method: 'POST',
        body: formdata,
    }).catch(error => console.log(error));
    let resJson = await res.json();
    return resJson.fileKey;
}

export async function uploadVideoFile(videoUrl)
{
    let formdata = new FormData();
    let blobFile = await fetch(videoUrl).then(r => r.blob());
    blobFile.lastModifiedDate = new Date();
    blobFile.name = new Date().getTime() + '.mp4';

    formdata.append('file', blobFile, blobFile.name);
    let res = await fetch("https://kmovie-subtitle-api.corp.kuaishou.com/rest/n/ty/cdn/file/cdn/uploadVideoBlobstore", {
        method: 'POST',
        body: formdata,
    }).catch(error => console.log(error));
    let resJson = await res.json();
    console.log(resJson);
    return resJson.blobstoreId;
}
