import React, {useState, useEffect} from 'react';
import styled from 'styled-components';
import download from 'downloadjs';
import {syntheticVideo} from '../utils';
import {t, Translate} from 'react-i18nify';
import {Spin, Space,Button} from 'antd';
import ArtplayerComponent from 'artplayer-react';
import { Input } from 'antd';
import { DownloadOutlined } from '@ant-design/icons';

const SyntheticVideo = styled.div`
    .item {
        margin-bottom: 20px;
        .title {
            color: #fff;
            font-size: 14px;
            padding: 5px 10px;
            margin-bottom: 10px;
            border-left: 2px solid #03a9f4;
        }
        .centent {
            padding: 0 10px;
            text-align: center;

            .info {
                font-size: 12px;
                color: rgba(255, 255, 255, 0.5);
                margin-bottom: 10px;
            }
            .warning {
                font-size: 12px;
                padding: 5px;
                color: #fff;
                background-color: rgba(199, 81, 35, 0.5);
                border: 1px solid rgba(199, 81, 35, 1);
            }
        }
    }
`;

export default function ({options, storage}) {
    const [videoUrl, setVideoUrl] = useState("");
    const [synthesis, setSynthesis] = useState(1);

    async function synthesising(options, storage){
        setSynthesis(2);
        setVideoUrl(await syntheticVideo(options, storage));
        setSynthesis(3);
    }
    useEffect(() => {
        synthesising(options, storage);
    }, []);
    return (
        <SyntheticVideo>
            <div className="item">
                <div className="centent">
                    {synthesis===1 &&
                    <Button type="primary" onClick={()=>synthesising(options, storage)}>合成</Button>
                    }
                    {synthesis===2 &&
                    <Space size="middle">
                        <Spin size="large"/>
                        <Translate value="synthetic-video-ing"/>
                    </Space>
                    }
                    {synthesis===3 &&
                    <div style={{height: "300px"}}>
                        <ArtplayerComponent
                            style={{
                                width: '100%',
                                height: '80%',
                            }}
                            option={{
                                url: videoUrl,
                                loop: true,
                                autoSize: true,
                                aspectRatio: true,
                                playbackRate: true,
                                fullscreen: true,
                                fullscreenWeb: true,
                                miniProgressBar: true,
                                moreVideoAttr: {
                                    crossOrigin: 'anonymous',
                                    preload: 'auto',
                                },
                            }}
                        />
                        <div className="info">
                            <br/>
                            <Input defaultValue={videoUrl} disable="true" addonAfter={<DownloadOutlined onClick={()=>download(videoUrl)} />} />
                        </div>
                    </div>
                    }
                </div>
            </div>
        </SyntheticVideo>
    );
}
